.main {
    @apply block h-[100%];
}


.section {
    @apply flex; 
}

/* ! =================================== MEDIA =================================== ! */

@media screen and (min-width: 820px) and (max-width: 1349px) {
    .main {
        @apply bg-[url("../../img/images/background.jpg")] py-[50px];
    }

    .temporary__container {
        @apply block mx-auto w-[540px] bg-white rounded-[8px];
        box-shadow: 0px 5px 20px rgba(108, 117, 139, 0.2);
    }
}

@media screen and (min-width: 1350px) {
    .main{
        @apply flex bg-[url("../../img/images/background.jpg")];
    }

    .temporary__container {
        @apply flex w-[100%];
    }

    .section {
        @apply w-[60%]
    }
}